export function Background() {
    return (
        <div className="fixed inset-0 -z-50 h-full w-full bg-black">
            {/* Raw Image - No Overlays, No Framework Components */}
            <img
                src="/bg-lush.png"
                alt="Background"
                className="h-full w-full object-cover opacity-100"
            />
        </div>
    )
}
